/* price component wrapper */
.price__wrapper {
  flex-basis: 33.33%;
  width: 33.33%;
}
/* price content */
.price__content {
  border-radius: 20px;
  background-color: white;
  padding: 0 23px;
  height: 67px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.price__content:hover {
  box-shadow: 3px 3px 9px rgba(0, 0, 0, 0.1);
  border: 1px solid #d3d3d3;
}
.price__content h3 {
  color: #8a959e;
  font-size: 12.5px;
  letter-spacing: 1px;
  margin-bottom: 10px;
}
.price__content p {
  color: black;
  font-size: 14.5px;
  letter-spacing: 1px;
}
.price__content__flex {
  display: flex;
  align-items: center;
}
/* input wrapper */
.price__input__wrapper {
  border-radius: 20px;
  padding: 13px 23px;
  background-color: white;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: white;
}
/* input slider */
.price__input {
  appearance: none;
  -webkit-appearance: none;
  background-color: #3bb3bd;
  height: 7px;
  margin: 1rem 0;
  opacity: 0.8;
}
/* input slider white circle */
.price__input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background-color: white;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 0 0 0 3px rgb(59, 179, 189);
}
.price__input::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background-color: white;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 0 0 0 3px rgb(59, 179, 189);
}
.price__input::-ms-thumb {
  width: 20px;
  height: 20px;
  background-color: white;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 0 0 0 3px rgb(59, 179, 189);
}
/* price cross */
.price__cross {
  background-color: #dadfe1;
  border-radius: 50%;
  color: gray;
  font-family: sans-serif;
  font-size: 12px;
  height: 20px;
  margin-left: 7px;
  position: relative;
  width: 20px;
}
.price__cross:hover {
  cursor: pointer;
}
.price__cross__x {
  fill: #8a959e;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}
