.card {
  border: none;
  border-radius: 24px;
  background-color: white;
  padding: 20px 24px;
  width: 605px;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
}
.card:hover {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border: 1px solid #d3d3d3;
}

.teacher * {
  margin: 0 0 0.9rem 0;
}
.teacher__name {
  font-weight: 700;
  font-size: 18px;
  display: flex;
  align-items: baseline;
}
.price {
  margin: 0;
}
.teacher__contact {
  display: flex;
  flex-direction: column;
}
.teacher__flex {
  display: flex;
  justify-content: space-around;
}
.teacher__flex__baseline {
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  margin-top: 8px;
}
.teacher__buttons {
  margin-top: 22px;
  background-color: #0096b2;
  padding: 11px 28px;
  border: none;
  border-radius: 7px;
  font-size: 16px;
  font-weight: 500;
  color: white;
  cursor: pointer;
}
.teacher__buttons:hover {
  opacity: 0.8;
}
